import React, { FC } from "react";
import s from "./Footer.module.scss";

const Footer: FC = () => {
  const infoArr: Array<{ header: string; text: Array<string> }> = [
    { header: "Get in touch", text: ["About Us", "Careers", "Press Releases", "Blog"] },
    { header: "Connections", text: ["Facebook", "Twitter", "Instagram", "Youtube", "LinkedIn"] },
    { header: "Earnings", text: ["Become an Affiliate", "Advertise your product", "Sell on Market"] },
    {
      header: "Account",
      text: ["Your account", "Returns Centre", "100 % purchase protection", "Chat with us", "Help"],
    },
  ];
  const tagsArr: Array<string> = [
    "Beans",
    "Carrots",
    "Apples",
    "Garlic",
    "Mushrooms",
    "Tomatoes",
    "Chilli peppers",
    "Broccoli",
    "Watermelons",
    "Oranges",
    "Bananas",
    "Grapes",
    "Cherries",
    "Meat",
    "Seo tag",
    "Fish",
    "Seo tag",
    "Fresh food",
    "Lemons",
  ];
  return (
    <div className={s.footer}>
      <div className={s.infoBlock}>
        {infoArr.map((el) => {
          return (
            <div className={s.infoBlock_section}>
              <h2>{el.header}</h2>
              {el.text.map((text) => (
                <p>{text}</p>
              ))}
            </div>
          );
        })}
      </div>
      <div className={s.tagsBlock}>
        <h2>Product tags</h2>
        <div className={s.tagsBlock_list}>
          {tagsArr.map((tag) => {
            return (
              <div>
                <p>{tag}</p>
              </div>
            );
          })}
        </div>
      </div>
      <div className={s.CopyrightBlock}>
        <p>Copyright  2020 petrbilek.com</p>
      </div>
    </div>
  );
};

export default Footer;
